html {
  max-width: 540px;
  min-width: 320px;
}
header {
  top: 0;
  height: 2.5707rem;
  font-size: 12px;
  width: 100%;
  z-index: 999;
  position: fixed;
  display: flex;
  flex-flow: nowrap column;
  background-color: #fafafa;
}
header img {
  height: 0.6693rem;
  width: 0.6693rem;
}
header > :nth-child(1) {
  flex: 1.286;
  display: flex;
  flex-flow: nowrap row;
  justify-content: space-between;
  align-items: none;
}
header > :nth-child(1) .left {
  display: flex;
  flex-flow: nowrap row;
  justify-content: none;
  align-items: none;
  float: 3;
}
header > :nth-child(1) .left h2 {
  font-size: 14px;
  color: #000;
  font-weight: 600;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 0.4rem;
}
header > :nth-child(1) .left p {
  line-height: 0.3067rem;
  margin-top: -0.1333rem;
  color: grey;
}
header > :nth-child(1) .left .img_icon {
  height: 100%;
  width: 50%;
  flex: 1;
  margin-right: 0.4067rem;
  margin-left: 0.4067rem;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
header > :nth-child(1) .left .qq_music {
  flex: 4.5;
}
header > :nth-child(1) .right {
  height: 100%;
  flex: 1;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
header > :nth-child(1) .right div {
  width: 2.2933rem;
  height: 0.6667rem;
  margin-right: -2rem;
  border-radius: 0.5333rem;
  border: solid 1px #d8d8d8;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
header > :nth-child(1) .right a {
  font-weight: 400;
  text-align: center;
  color: #575656;
}
header > :nth-child(2) {
  flex: 1;
  display: flex;
  flex-flow: nowrap row;
  justify-content: none;
  align-items: none;
}
header > :nth-child(2) div {
  flex: 1;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
}
header > :nth-child(2) div span {
  font-size: 0.3633rem;
  line-height: 1rem;
  width: 1.6rem;
  text-align: center;
  display: inline-block;
}
header > :nth-child(2) div .active {
  color: #22d59c;
  border-bottom: 3.5px solid #22D59C;
}
.tab_cont {
  margin: 3.1667rem auto;
}
.tab_cont .search_box {
  width: 100%;
  height: 1.0667rem;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  position: relative;
}
.tab_cont .search_box .search_btn {
  width: 90%;
  height: 90%;
  text-align: center;
  font-size: 0.3733rem;
  font-weight: 400;
  border-radius: 0.8rem;
  background-color: #fafafa;
}
.tab_cont .search_box i {
  font-size: 0.4733rem;
  font-weight: 700;
}
.tab_cont .search_box .search {
  width: 70%;
  text-align: start;
  width: 91%;
  margin: 2rem auto;
  height: 8.56rem;
}
.tab_cont .search_icon {
  width: 0.4rem;
  height: 0.4rem;
  font-size: 0.6667rem;
  font-weight: 700;
  position: absolute;
  left: 39%;
  top: 20%;
}
.rank_list,
.search_btn,
.search,
.rank_list {
  width: 91%;
  margin: 2rem auto;
  height: 8.56rem;
}
.hot {
  width: 91%;
  margin: 1.4rem auto;
}
.rank_list {
  width: 96%;
  margin: 2.9rem auto;
}
.history {
  width: 91%;
  margin: 2rem auto;
}
.list {
  width: 100%;
  height: 2.8073rem;
  display: flex;
  margin-top: 1.0667rem;
  flex-flow: nowrap row;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
}
.list .top_list {
  width: 6.2667rem;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
}
.list h5 {
  font-size: 0.3567rem;
  line-height: 0rem;
  margin-bottom: 0.05rem;
  margin-top: -0.05rem;
}
.list p {
  font-size: 0.3467rem;
  font-weight: 300;
  line-height: 0rem;
  color: rgba(26, 26, 26, 0.5);
  margin-bottom: 0.07rem;
}
.list p strong {
  font-size: 0.0733rem;
  color: #1a1a1a;
}
.list .top_list i {
  font-size: 0.11rem;
  line-height: 0rem;
  margin-bottom: 0rem;
}
.footers {
  width: 100%;
  height: 2.754rem;
  margin-bottom: 3rem;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.footers div:nth-child(1) {
  width: 2.02rem;
  height: 0.6567rem;
  margin: 0 auto;
  background: url(../image/logo_ch.svg) center center no-repeat;
  background-size: 100% 100%;
}
.footers div:nth-child(n+1) {
  height: 0.4827rem;
  text-align: center;
}
.footers div {
  color: #1a1a1a;
}
footer {
  width: 100%;
  height: 2.0536rem;
  bottom: 0.4rem;
  box-shadow: 0 3px 25px rgba(0, 0, 0, 0.05);
  position: fixed;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
}
footer .foot {
  background-color: #fafafa;
  width: 5.4667rem;
  height: 1.2rem;
  border-radius: 1.3333rem;
  display: flex;
  flex-flow: nowrap row;
  justify-content: none;
  align-items: none;
}
footer .foot :nth-child(1) {
  flex: 1;
  background: url(../image/logo.svg) center center no-repeat;
  background-size: 0.5867rem 0.5867rem;
}
footer .foot :nth-child(2) {
  height: 100%;
  flex: 5;
  font-size: 13px;
  color: #1a1a1a;
  line-height: 1.2rem;
  text-align: center;
  white-space: nowrap;
}
